.voice {
  text-align: center;
  line-height: 1.75;
}

.voice__inner {
  border-top: 3px solid #d4145a;
  border-bottom: 3px solid #d4145a;
  position: relative;
  padding-top: 1.5625rem;
  padding-bottom: 3.125rem;
  margin-bottom: 5.5rem;
}

.voice__inner::before,
.voice__inner::after {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  background: linear-gradient(to right, #d4145a 0%, #d4145a 50%, #ffffff 51%, #ffffff 100%) repeat-x left bottom/6px 1px;
}

.voice__inner::before {
  top: 0.5rem;
}

.voice__inner::after {
  bottom: 0.5rem;
}

.voice__head-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.375rem;
  margin-bottom: 2.125rem;
}

.voice__image--mb80 {
  margin-bottom: 5rem;
}

.voice__image--mb60 {
  margin-bottom: 3.75rem;
}

.voice__head {
  flex-shrink: 0;
  font-size: 1.1875rem;
}

.voice__text--mb66 {
